.vcRoot{
     background-color: rgb(226, 222, 226);
    height: 100%;
}

.vcImg{
       margin: 50px auto;
      display: block;
      height: 400px;
}

.vcHeader{
     margin: 30px 50px;
    height: 400;
    padding: 10px;
    text-align: center;
    color:#053232;
}

.vcHeader h1{
      font-size: 50px;
        font-family: 'Oswald', sans-serif;
        font-weight: 300;
        line-height: 0.8;
}

.vcHeader h2{
      font-size: 30px;
        font-weight: 600;
}

.vcHeader p{
    font-size: 20px;
        font-weight: 400; 
        text-indent: 50px;
        margin-top: 20px;
}